*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	border: 0;
	box-sizing: border-box;
}

*::-webkit-scrollbar {
	width: 0;
	height: 0;
}

li {
	list-style: none;
}

a,
a:visited {
	text-decoration: none;
}

a:hover {
	text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: inherit;
	font-size: inherit;
}

img {
	vertical-align: top;
}

html,
body {
	height: 100%;
}

html {
	font-size: 16px;
	scroll-behavior: smooth;
}

body {
	line-height: 1;
	overflow-x: hidden;

}

.wrapper {
	min-height: 100%;
	position: relative;
}